*,*::before,*::after {margin:0em;padding:0em;}

div {
   font-family: sans-serif;
   text-align: center;
   width: 100%;
   min-height: 100vh;
   display: flex;
   justify-content: center;
   align-items: center;
   background:antiquewhite;
}

button {
   background-color: blueviolet;
   border:none;
   padding: 0.5em 1em;
   color: white;
   margin-top: 0.6em;
   font: bold 1.2rem/normal sans-serif;
   cursor: pointer;
   margin-inline: 10px;
   border: 2px solid #fff;
}

div {
   display: flex;
   flex-direction: column;
}

div > input {
   margin-top:1em;
   padding: 1em;
}